/* eslint-disable import/no-anonymous-default-export */
import tw from 'twin.macro';
import styled from 'styled-components';
import * as ROUTES from '../../constants/routes';
import Header, {
	NavLink,
	NavLinks,
	PrimaryLink,
	LogoLink,
	NavToggle,
	DesktopNavLinks,
} from '../Header/light.js';

import { Link as ReactRouterLink } from 'react-router-dom';

const StyledHeader = styled(Header)`
	${tw`pt-8 max-w-none`}
	${DesktopNavLinks} ${NavLink}, ${LogoLink} {
		${tw`text-gray-100 hover:border-gray-300 hover:text-gray-300`}
	}
	${NavToggle}.closed {
		${tw`text-gray-100 hover:text-primary-500`}
	}
`;
const Container = styled.div`
	${tw`relative -mx-8 -mt-8 bg-center bg-cover`}
	background: rgb(0,0,10);
	/* background-image: url('https://images.unsplash.com/photo-1522071901873-411886a10004?ixlib=rb-1.2.1&auto=format&fit=crop&w=1920&q=80'); */
`;

const OpacityOverlay = tw.div`z-10 absolute inset-0 bg-primary-500 opacity-25`;

const HeroContainer = tw.div`z-20 relative px-4 sm:px-8 max-w-screen-xl mx-auto`;
const TwoColumn = tw.div`pt-24 pb-32 px-4 flex justify-between items-center flex-col lg:flex-row`;
const LeftColumn = tw.div`flex flex-col items-center lg:block`;
const RightColumn = tw.div`w-full sm:w-5/6 lg:w-1/2 mt-16 lg:mt-0 lg:pl-8`;

const Heading = styled.h1`
	${tw`text-3xl text-center lg:text-left sm:text-4xl lg:text-5xl xl:text-6xl font-black text-gray-100 leading-none`}
	span {
		${tw`inline-block mt-2`}
	}
`;

const SlantedBackground = styled.span`
	${tw`relative text-primary-500 px-4 -mx-4 py-2`}
	&::before {
		content: '';
		${tw`absolute inset-0 bg-gray-100 transform -skew-x-12 -z-10`}
	}
`;

const Notification = tw.span`inline-block my-4 pl-3 py-1 text-gray-100 border-l-4 border-blue-500 font-medium text-sm`;

const PrimaryAction = tw.button`px-8 py-3 mt-10 text-sm sm:text-base sm:mt-16 sm:px-8 sm:py-4 bg-gray-100 text-primary-500 font-bold rounded shadow transition duration-300 hocus:bg-primary-500 hocus:text-gray-100 focus:shadow-outline`;

const Link = styled(ReactRouterLink)`
	border-radius: 20px;
`;
export default () => {
	const navLinks = [
		<NavLinks key={1}>
			<NavLink href={ROUTES.ABOUT_US}>About</NavLink>
			<NavLink href={ROUTES.PRODUCTS}>Systems</NavLink>
		</NavLinks>,
		<NavLinks key={3}>
			<PrimaryLink href={ROUTES.CONTACT_US}>Contact Us</PrimaryLink>
		</NavLinks>,
	];

	return (
		<Container>
			<OpacityOverlay />
			<HeroContainer>
				<StyledHeader links={navLinks} />
				<TwoColumn>
					<LeftColumn style={{ position: 'relative', top: '-50px' }}>
						<Notification>
							We're launching our operations internationally too{' '}
						</Notification>
						<Heading>
							<span style={{ fontSize: '30px' }}>
								access advanced systems
							</span>
							{'  '}
							<span> We are the best</span>
							<br />
							<SlantedBackground>at what we do</SlantedBackground>
						</Heading>
						<Link to={ROUTES.ABOUT_US}>
							<PrimaryAction
								style={{
									marginLeft: '.8rem',
									borderRadius: '20px',
									textTransform: 'uppercase',
								}}
							>
								Read More
							</PrimaryAction>
						</Link>
						<Link to={ROUTES.PRODUCTS}>
							<PrimaryAction
								style={{
									marginLeft: '.8rem',
									borderRadius: '20px',
									textTransform: 'uppercase',
									height: '4rem',
								}}
							>
								Get Started
							</PrimaryAction>
						</Link>
					</LeftColumn>
					<RightColumn>
						<video
							src='/videos/advert.mp4'
							autoPlay
							loop
							muted
							style={{
								maxWidth: '95%',
								alignSelf: 'center',
								height: 'auto',
								borderRadius: '5px',
								marginLeft: '15px',
								boxShadow: '0px 0px 2px 4px #5668878c',
							}}
						/>
					</RightColumn>
				</TwoColumn>
			</HeroContainer>
		</Container>
	);
};
